header{
    height: 170px;
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    z-index: 999;
    .container{
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
    .list-wrapper{
        display: flex;
        align-items: center;
        a{
            padding: 0 25px;
            font-size: 15px;
            line-height: 1.2;
            font-weight: 600;
            transition: $transition;
            &:hover{
                color: $orange;
            }
            &.active{
                color: $orange;
            }
        }
    }
    .button{
        border-radius: 22px;
        border: 1px solid $border-grey;
        padding: 16px 50px;
        color: $blue;
        font-size: 16px;
        font-weight: 600;
    }
}

