footer{
    position: fixed;
    left: 0px;
    bottom: 0px;
    z-index: 999;
    width: 100%;
    height: 180px;
    .container{
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .contact{
        display: flex;
        flex-direction: column;
        justify-content: center;
        .mail-wrapper{
            display: flex;
            .mail{
                padding-left: 15px;
                display: flex;
                justify-content: center;
                flex-direction: column;
            }
            .name{
                font-size: 12px;
                color: $grey;
                font-weight: 600;
            }
            .link{
                font-size: 16px;
                font-weight: 700;
                color: $orange;
            }
        }
        .img-wrapper{
            img{
                width: 24px;
                height: auto;
                object-fit: cover;
            }
        }
        .img-wrapper{
            width: 50px;
            height: 50px;
            background: $white;
            border-radius: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        .copiryght{
            padding-top: 20px;
            font-size: 13px;
            color: $grey;
            line-height: 2;
        }
    }
    .pages{
        display: flex;
        flex-direction: column;
        align-items: center;
        .number{
            font-size: 48px;
            line-height: 1;
            letter-spacing: 0.48px;
            color: $orange;
            border-bottom: 2px solid $blue;
        }
        img{
            width: 18px;
            height: 24px;
            object-fit: cover;
            margin-bottom: 20px;
        }
        .name{
            font-size: 15px;
            color: $ligh-grey-color;
            text-align: center;
            line-height: 1.8;
        }
    }
}