.main-window{
    height: 100vh;
    background: $bg-color;
    .container{
        align-items: center;
        justify-content: center;
    }
    .title{
        text-align: center;
        color: $blue;
        font-size: 140px;
        font-weight: 700;
        text-align: center;
        line-height: 1;
        margin-bottom: 20px;
    }
    .text{
        font-size: 18px;
        font-weight: normal;
        text-align: center;
        line-height: 1.6;
    }
}

.about{
    height: 100vh;
    .container{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
    }
    .title-wrapper{
        min-width: max-content;
        padding-right: 150px;
        .title{
            color: $blue;
            font-weight: 300;
            font-size: 30px;
        }
    }
    .content-wrapper{
        width: 750px;
        max-width: 45%;
        padding-right: 250px;
        .title{
            color: $black;
            font-size: 48px;
            font-weight: 700;
            padding-bottom: 20px;
        }
        .text{
            color: $grey;
            font-size: 15px;
            padding-bottom: 30px;
        }
        .partner-wrapper{
            display: flex;
            .text{
                font-weight: 600;
                font-size: 14px;
                color: $dark-grey-color;
                padding-right: 40px;
            }
            .list-wrapper{
                display: flex;
                padding: 5px 0;
                .list-item{
                    padding: 0 20px;
                }
                img{
                    width: auto;
                    height: 32px;
                    max-width: 40px;
                    object-fit: contain;
                }
            }
        }

    }
    .provide-wrapper{
        width: 750px;
        max-width: 45%;
        padding-right: 250px;
        .title{
            color: $blue;
            font-size: 30px;
            font-weight: 300;
            padding-bottom: 50px;
        }
        .list-wrapper{
            .list-item{
                display: flex;
                align-items: center;
                padding-bottom: 40px;
                .text{
                    color: $blue;
                    font-size: 16px;
                    font-weight: 600;
                }
                img{
                    width: 48px;
                    height: 48px;
                    object-fit: contain;
                    margin-right: 20px;
                }
            }
        }
    }
}